* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

header {
  background-color: gold;
  height: 100px;
  line-height: 100px;
  text-align: center;
  vertical-align: middle;
}

article {
  width: 80%;
  margin: auto;
}

div.pageTitle {
  vertical-align: middle;
  padding: 20px;
}

table {
  width: 100%;
  border-collapse: collapse;
  border-top: 3px solid pink;
  border-bottom: 3px solid pink;
}

th,
td {
  padding: 10px;
  text-align: center;
}

th {
  border-bottom: 1px solid pink;
}

td {
  border-bottom: 1px dotted pink;
}

th.contentTitle {
  width: 50%;
}

table tr td:nth-child(2) {
  text-align: left;
}

table.formTable th,
table.formTable td {
  padding: 10px;
  text-align: center;
  border-bottom: 1px dotted pink;
}

table.formTable th {
  border-right: 1px solid pink;
  min-width: 150px;
}

table.formTable th.inTh {
  border-left: 2px solid pink;
  width: 150px
}

table.formTable tr.content {
  border-top: 2px solid pink;
  height: 500px;
  vertical-align: top;
}

div.buttons {
  padding: 20px;
  text-align: right;
}

button {
  padding: 10px;
  border: 1px solid pink;
  margin: 0 10px;
}